/* Estilo geral da tela */
.sobre-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px;
    background: linear-gradient(180deg, #001428 0%, #001f3f 100%); /* Degradê do menu (#001428) ao fundo (#001f3f) */
    color: white;
    min-height: 100vh;
  }
  
  .sobre-conteudo {
    max-width: 50%;
  }
  
  .sobre-titulo {
    font-size: 4rem;
    margin-bottom: 20px;
    color: white;
  }
  
  .sobre-descricao {
    font-size: 1.2rem;
    line-height: 1.8;
    color: #d1d1d1; /* Cor para contraste */
  }
  
  .sobre-imagem {
    max-width: 40%;
  }
  
  .imagem-destaque {
    width: 100%;
    max-width: 400px;
    object-fit: cover;
    border-radius: 10px; /* Arredondar as bordas */
  }